import { notFound } from 'next/navigation'

const PROJECTS = {
  'project-alpha': {
    title: 'Project Alpha',
    desc: 'Detailed case study for Project Alpha. Add real client data and screenshots here.'
  },
  'project-beta': {
    title: 'Project Beta',
    desc: 'Detailed case study for Project Beta.'
  },
  'project-gamma': {
    title: 'Project Gamma',
    desc: 'Detailed case study for Project Gamma.'
  },
}

export default function ProjectPage({ params }) {
  const data = PROJECTS[params.slug]

  if (!data) return notFound()

  return (
    <main className="py-20">
      <div className="container mx-auto">
        <h1 className="text-4xl font-bold mb-4">{data.title}</h1>
        <p className="text-gray-700">{data.desc}</p>

        <div className="mt-8 p-6 bg-white/60 backdrop-blur rounded shadow">
          ðŸ“¸ Add screenshots, tech stack, mockups, and project links here.
        </div>
      </div>
    </main>
  )
}
